<template>
  <div class="box">
    <div class="navlist">
      <div class="l">茶叶</div>
      <div class="r">
        <ul>
          <li v-for="(i, $index) in getarr" :key="$index" @click="btn($index),$store.state.type = i" :class="index == $index ? 'active' : ''">
            {{i}}
          </li>
        </ul>
      </div>
    </div>
    <div class="item_box">
      <div class="item">
        <div class="list">
          <ul>
            <li v-for="(i, $index) in listarr" :key="$index" class="animate__animated animate__slideInUp">
              <router-link :to="'/teadetails/'+i.id">
                <div>
                <div class="img"><img :src="i.imgurl" alt="" /></div>
                <div class="text">
                  <p class="smtitle">{{ i.title }}</p>
                  <div class="line"></div>
                  <p class="money">{{ i.money }}</p>
                </div>
                <div class="btn"><button>→</button></div>
              </div>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.navlist {
  padding: 20px 40px;
  display: flex;
  background-color: #f9f9f9;
  .l {
    width: 25%;
    padding: 0 5px;
    box-sizing: border-box;
    font-family: 微软雅黑;
    font-size: 24px;
    color: #333333;
    padding: 0 0 15px 0;
  }
  .r {
    flex: 1;
    padding: 0 5px;
    box-sizing: border-box;
    ul {
      text-align: right;
      margin-top: 5px;
      li {
        display: inline-block;
        padding: 10px 0;
        margin-right: 15px;
        width: 60px;
        text-align: center;
      }
      li:hover {
        color: #90af6b;
        border-bottom: solid 1px #90af6b;
      }
      .active {
        color: #90af6b;
        border-bottom: solid 1px #90af6b;
      }
    }
  }
}
.item_box {
  .item {
    margin-left: 30px;
    margin-right: 30px;
    .list {
      ul {
        overflow: hidden;
        li {
          width: 20%;
          border-bottom: 1px solid transparent;
          padding: 15px;
          float: left;
          box-sizing: border-box;
          a{
              > div {
              margin: 0 20px;
              .img {
                overflow: hidden;
                padding-bottom: 10px;
                img {
                  width: 100%;
                  transition: 0.5s;
                }
              }
              .line {
                width: 0;
                height: 1px;
                transition: 0.5s;
                background-color: #519f10;
              }
              .text {
                .smtitle {
                  padding-bottom: 15px;
                  color: #555555;
                  line-height: 16px;
                }
                .money {
                  padding: 10px 0 15px 0;
                  color: #555555;
                  line-height: 22px;
                }
              }
              .btn{
                button{
                  color: #d1d1d1;
                  font-size: 13px;
                  font-family: 微软雅黑;
                  padding: 5px 8px;
                  border: solid 1px #d8d8d8;
                }
              }
            }
          }
        }
        li:hover {
          .img img {
            transform: scale(1.1);
          }
          .line {
            width: 100%;
          }
          .text .smtitle {
            color: #519f10;
          }
          .btn button{
            border-color: #519f10;
            color: #519f10;
          }
        }
      }
    }
  }
}
</style>
<script>
import {mapGetters} from 'vuex'

export default {
  data() {
    return {
      index: 0,
    };
  },
  methods: {
    btn(index) {
      this.index = index;
    },
  },
  computed: {
   ...mapGetters(['getarr','listarr']),
  },
};
</script>
