                    <router-link to="/tea"><li>茶叶</li></router-link>
                    <li class="arrow">&gt;</li>
                    <router-link to="/tea"><li>全部</li></router-link>
                    <li class="arrow">&gt;</li>
                    <li v-html="nameitem[0].type"></li>
                    <li class="arrow">&gt;</li>
                    <li v-html="nameitem[0].title" class="current"></li>
                </ul>
            </div>
        </div>
        <div class="item_box">
            <div class="item">
                <div class="content_box" v-for="(i,$index) in nameitem" :key="$index">
                    <div class="l">
                        <div class="bigimg">
                            <img :src="i.imgurl" alt="">
                            <div class="bg"></div>
                        </div>
                        <div class="enlarge">
                            <img :src="i.imgurl" alt="">
                        </div>
                        <div class="smimg">
                            <img :src="i.imgurl" alt="">
                        </div>
                    </div>
                    <div class="r">
                        <div class="title">{{i.title}}</div>
                        <div class="money">{{i.money}}</div>
                        <div class="text">{{i.text}}</div>
                        <div class="number">
                            <p>商品编号：{{i.num}}</p>
                            <p>分类：<span>{{i.type}}</span></p>
                        </div>
                        <div class="btn">
                            <router-link to="/shopping"><button @click="btn(i)">加入购物车</button></router-link>
                            <div class="sminput">
                                <button @click="reduce">-</button>
                                <input type="text" value="1" v-model="num">
                                <button @click="plus">+</button>
                            </div>
                        </div>
                        <div class="collect">
                            <i></i><span>收藏</span>
                        </div>
                    </div>
                </div>
                <div class="describe">
                    <div class="title">
                        描述
                    </div>
                    <div class="text">
                        饮茶不但是传统饮食文化，同时，由于茶中含有多种抗氧化物质与抗氧化营养素，对于消除自由基有一定的效果。因此喝茶也有助防老，具养生保健功能，每天喝三两杯茶可起到防老的作用。茶叶中含有多种维生素和氨基酸，喝茶对于清油解腻，增强神经兴奋以及消食利尿也具有一定的作用。 但并不是喝得越多越好，也不是所有的人都适合喝茶。一般来说，每天1-2次，每次3-5克、400毫升的饮量是比较适当的。一般，人们说喝茶还指喝水。
                    </div>
                    <div class="img"><img src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC9iYmM0ZTQxZDFlYjVmNTk2NmE0MzIyYTZhMjhjNDVmZC05MC53ZWJw.webp" alt=""></div>
                </div>
                <div class="relevant" v-show="relevant != ''">
                    <h3>相关商品</h3>
                    <div class="cont" v-for="(i,$index) in relevant" :key="$index">
                        <router-link :to="'/teadetails/'+i.id">
                            <img :src="i.imgurl" alt="">
                            <p class="smtitle">{{i.title}}</p>
                            <p class="price">{{i.money}}</p>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>
<style lang="scss" scoped>
    .crumbs{
        padding: 20px 40px;
        div{
            padding: 0 5px;
            ul{
                color: #444444;
                font-size: 14px;
                display: flex;
                .current{
                    color: #90AF6B;
                }
                .arrow{
                     width: 20px;
                     text-align: center;
                }
            }
        }
    }
    .item_box{
        max-width: 1024px;
        margin: 0 auto;
        padding-top: 40px;
        .item{
            padding: 0 40px;
            .content_box{
                padding: 0 5px;
                display: flex;
                .l{
                    width: 45%;
                    margin-bottom: 2em;
                    position: relative;
                    .bigimg{
                        img{
                            width: 100%;
                        }
                        .bg{
                            width: 200px;
                            height: 200px;
                            background-color: red;
                            position: absolute;
                            left: 0;
                            top: 0;
                            display: none;
                        }
                    }
                    .enlarge{
                        width: 420px;
                        height: 420px;
                        position: absolute;
                        left: 100%;
                        top: 0;
                        overflow: hidden;
                        display: none;
                        z-index: 2;
                        background-color: #fff;
                        img{
                            width: 300%;
                            height: 300%;
                        }
                    }
                    .smimg{
                        padding-top: 1em;
                        img{
                            width: 93px;
                            height: 93px;
                            margin-right: 16px;
                        }
                    }
                }
                .r{
                    flex: 1;
                    padding-left: 40px;
                    .title{
                        font-size: 22px;
                        color: #444;
                        margin-bottom: 20px;
                        position: relative;
                    }
                    .title::after{
                        content:'';
                        position: absolute;
                        left: 0;
                        bottom: -20px;
                        width: 30px;
                        height: 2px;
                        background-color: #ddd;
                    }
                    .money{
                        padding-top: 20px;
                        margin-bottom: 20px;
                        font-size: 22px;
                        color: #444;
                    }
                    .text{
                        margin-bottom: 20px;
                        line-height: 20px;
                        font-size: 13px;
                        color: #939393;
                    }
                    .number{
                        margin-bottom: 20px;
                        p{
                            padding: 7px 0;
                            font-size: 14px;
                            color: #777;
                            border-top: 1px dotted #ddd;
                            span{
                                color: #627f9a;
                            }
                        }
                    }
                    .btn{
                        margin-bottom: 20px;
                        a{
                            button{
                                padding: 13px 24px;
                                color: #fff;
                                border-color: #519f10;
                                background-color: #519f10;
                                margin-right: 15px;
                            }
                        }
                        .sminput{
                            display: inline-block;
                            button{
                                width: 32px;
                                height: 44px;
                                font-size: 14px;
                                color: #333;
                                border: solid 1px #ccc;
                            }
                            input{
                                width: 32px;
                                height: 42px;
                                border: solid 1px #CCCCCC;
                                border-left: none;
                                border-right: none;
                                text-align: center;
                            }
                        }
                    }
                    .collect{
                        font-size: 12px;
                        color: #666;
                    }
                }
            }
            .describe{
                padding: 0 5px 80px 5px;
                .title{
                    width: 60px;
                    height: 44px;
                    line-height: 44px;
                    text-align: center;
                    font-size: 15px;
                    font-weight: bold;
                    color: #333;
                    border-top: 4px solid #333
                }
                .text{
                    margin-top: 15px;
                    line-height: 24px;
                    color: #777;
                    font-size: 15px;
                }
                .img{
                    margin-top: 15px;
                    img{
                        width: 100%;
                    }
                }
            }
            .relevant{
                h3{
                    font-size: 20px;
                    font-weight: normal;
                    color: #555555;
                    margin-bottom: 10px;
                }
                .cont{
                    width: 25%;
                    padding-right: 15px;
                    padding-bottom: 15px;
                    img{
                        width: 100%;
                    }
                    .smtitle{
                        color: #519f10;
                        margin-bottom: 6px;
                    }
                    .price{
                        color: #666;
                    }
                }
            }
        }
    }
</style>
<script>

export default {
    data() {
        return {
            num: 1,
            arr:[]
        }
    },
    methods: {
        reduce(){
            this.num--
            this.nameitem[0].n--
            if(this.num<=1){
                this.num = 1
            }
            if(this.nameitem[0].n <= 1){
                this.nameitem[0].n = 1
            }
        },
        plus(){
            this.num++
            this.nameitem[0].n++
        },
        moveHandler(){
            // console.log(event)
            let bg = document.querySelector('.bg')
            let enlarge = document.querySelector('.enlarge')
            // let l = document.querySelector('.l')
            
            //let item_box = document.querySelector('.item_box')
            
            bg.style.display = 'block'
            enlarge.style.display = 'block'
            // console.log(item_box.offsetHeight)
            
            // let X = event.clientX  -  bg.offsetWidth/2
            // let Y = event.clientY  - bg.offsetWidth/2
            // console.log(X,Y)
            // bg.style.left = X+'px'
            // bg.style.top = Y+'px'

            
        },
        outHandler(){
            // console.log(event)
            let bg = document.querySelector('.bg')
            let enlarge = document.querySelector('.enlarge')
            bg.style.display = 'none'
            enlarge.style.display = 'none'
        },
        btn(i){
            this.$store.commit('shoplist',i)
        }
    },
    computed:{
        nameitem(){
            let arr = []
            for (let i = 0; i < this.$store.state.arr.length; i++) {
                if(this.$route.params.id == this.$store.state.arr[i].id){
                    arr.push(this.$store.state.arr[i])
                }
            }
            return arr
        },
        relevant(){
            let newarr = []
            for (let i = 0; i < this.$store.state.arr.length; i++) {
                if(this.nameitem[0].type == this.$store.state.arr[i].type && this.nameitem[0].id != this.$store.state.arr[i].id){
                    newarr.push(this.$store.state.arr[i])
                }
            }
            return newarr
        }
    },
    mounted() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    },
    watch:{
        '$route.params.id'(){
            this.id = this.$route.params.id
        }
    }
}
</script>