import navlist from './components/navlist.vue'
import banner from './components/banner.vue'
import newtea from './components/newtea.vue'
import aboutme from './components/aboutme.vue'
import smimg from './components/smimg.vue'
import footers from './components/footers.vue'
import abouttea from './components/abouttea.vue'
import concat from './components/concat.vue'
import plant from './components/plant.vue'
import tealist from './components/tealist.vue'
import newslist from './components/newslist.vue'


let publics = {
    install(Vue){
        Vue.component('navlist',navlist)
        Vue.component('banner',banner)
        Vue.component('newtea',newtea)
        Vue.component('aboutme',aboutme)
        Vue.component('smimg',smimg)
        Vue.component('footers',footers)
        Vue.component('abouttea',abouttea)
        Vue.component('concat',concat)
        Vue.component('plant',plant)
        Vue.component('tealist',tealist)
        Vue.component('newslist',newslist)
    }
}

export default publics