<template>
    <!-- banner -->
    <div class="banner">
        <div class="banner-img">
            <ul class="carousel">
            <li class="selection"></li>
            <li></li>
        </ul>
        <ul class="circle">
            <li class="current"></li>
            <li></li>
        </ul>
        </div>
    </div>
</template>
<script>
export default {
    mounted() {
         var currentIndex = 0;//当前图片的索引，我希望从0开始
        var id;
        var len = document.querySelector('.carousel').childElementCount;//得到ul下面li的个数
        function carousel(index) {//建一个函数，用来提供一个索引然后切换到索引对应的图片
            if (currentIndex === len){ //判断一下，当前图片索引是否等于li的个数，因为当前索引是从0开始的，所以总数的值是没有的
                currentIndex = index = 0
            }
            if (currentIndex < 0) {
                currentIndex = index = 2
            }
            document.querySelector('.current').className = '';//将页面上有选中样式的小点的样式清空
            document.querySelector('.circle li:nth-child(' + (index + 1) + ')').className = 'current'//根据索引来控制那个小点是显示状态
            document.querySelector('.selection').className = '';//将页面上有选中样式的图片的样式清空
            document.querySelector('.carousel li:nth-child(' + (index + 1) + ')').className = 'selection'//根据索引来控制那张图片显示
        }

        function next() {//前进函数
            currentIndex++;//当前索引加1
            carousel(currentIndex) //调用carousel函数
        }
        var lis = document.querySelectorAll('.circle li');//得到页面上的所有小点
        for (var i = 0; i < lis.length; i++) {//for循环遍历
            lis[i].index = i;//将i值先保存一下
            lis[i].onclick = function () {//
                currentIndex = this.index
                carousel(currentIndex);
            }
        }
        function auto() {//自动切换
            id = setInterval(next, 5000)//间隔函数，每一秒调用一次前进函数
        }
        function stop() {
            clearInterval(id)
        }
        auto()
        document.querySelector('div').onmouseover = stop//div的鼠标移入事件
        document.querySelector('div').onmouseout = auto//div的鼠标移出事件
    },  
}
</script>
<style scoped>
    .banner{
        height: 750px;
        /* height: 100%; */
        margin-top: -100px;
    }
    .banner-img{
        position: relative;
        height: 100%;
        width: 100%;
    }
    .carousel{
        position:absolute;
        width:100%;
        height:100%;
    }
      .carousel li{
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        width:100%;
        height:100%;
        transition: opacity .5s;
    }
    .carousel li:nth-child(1){
        background-repeat: no-repeat;
        background-image: url('https://ccdn.goodq.top/caches/9b69daf…/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS81OTE2NjgzNjA1NzgxOWEwNWM0ZmY2ZDk1YWMzNTQ4Yi05MC53ZWJw.webp');
        background-size: cover;
        background-position: center center;
    }
    .carousel li:nth-child(2){
        background-repeat: no-repeat;
        background-image: url('https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS82MWE3MmU5YWE1OGE3NWFmNjRkYWUyODNhMDMwNDU0MC05MC53ZWJw.webp');
        background-size: cover;
        background-position: center center;
    }
       .carousel .selection {
        opacity: 1;
        z-index: 10;
    }
    .circle {
        position: absolute;
        height: 8px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 200;
        bottom: 40px;
    }
    .circle li {
        width: 60px;
        height: 6px;
        background: white;
        border: 1px solid transparent;
        float: left;
        margin-left: 10px;
        cursor: pointer;
    }
    .circle .current {
        background:#85bf41;
    }
</style>