let getters = {
  // 茶叶列表
  getarr(state) {
    let newarr = ['全部'];
    for (let i = 0; i < state.arr.length; i++) {
      if (newarr.indexOf(state.arr[i].type) == -1) {
        newarr.push(state.arr[i].type);
      }
    }
  return newarr
  },
  listarr(state){
    let list = []
    for (let i = 0; i < state.arr.length; i++) {
        if(state.type == '全部'){
            list.push(state.arr[i])
        }else if(state.type == state.arr[i].type)
        list.push(state.arr[i])
    }
    return list
  },

  // // 新闻列表
  teaarr(state){
    let arr = []
    for (let i = 0; i < state.newlist.length; i++) {
      if(arr.indexOf(state.newlist[i].type) == -1){
        arr.push(state.newlist[i].type)
      }
    }
    return arr
  },
  newslist(state){
    let arr = []
    for (let i = 0; i < state.newlist.length; i++) {
      if(state.newstype == '全部新闻'){
        arr.push(state.newlist[i])
      }else if(state.newstype == state.newlist[i].type)
      arr.push(state.newlist[i]) 
    }
    return arr
  }
}

let mutations = {
  shoplist(state,i){
    let type = true
    for (let a = 0; a < state.shoplist.length; a++) {
      if(i.id == state.shoplist[a].id){
        state.shoplist[a].n++
        type = false
      }
    }
    if(type){
      state.shoplist.push(i)
    }
    localStorage.setItem('shoplist',JSON.stringify(state.shoplist))
  },
  rmshoplist(state,i){
    state.shoplist.splice(i,1)
    // console.log(state.shoplist)
    localStorage.setItem('shoplist',JSON.stringify(state.shoplist))
  }
}

let actions = {

}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})
