import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import tea from '../views/tea.vue'
import base from '../views/base.vue'
import news from '../views/news.vue'
import tel from '../views/tel.vue'
import teadetails from '../views/teadetails.vue'
import newsdetails from '../views/newsdetails.vue'
import shopping from '../views/shopping.vue'

Vue.use(VueRouter)

const routes = [
  {path:'/',redirect:'Home'},
  {path:'/Home', name: 'Home',component: Home},
  {path:'/about',name: 'About',component: About},
  {path:'/tea',name:'tea',component:tea},
  {path:'/base',name:'base',component:base},
  {path:'/news',name:'news',component:news},
  {path:'/tel',name:'tel',component:tel},
  {path:'/teadetails/:id',name:'teadetails',component:teadetails},
  {path:'/newsdetails/:id',name:'newsdetails',component:newsdetails},
  {path:'/shopping',name:'shopping',component:shopping},
]

const router = new VueRouter({
  // mode: 'history',
  routes,
  linkActiveClass: 'active',
})

export default router
