<template>
    <div class="box">
        <div v-if="arr.length == 0" style="text-align:center;margin: 60px auto 120px;color: #777777;">购物车为空，请您先添加商品到购物车中！</div>
        <div class="item_box" v-if="arr.length!=0">
            <div class="item">
                <div class="content">
                    <div class="l_list">
                        <div class="table">
                            <div class="hd">
                                <span>商品</span><span>价格</span><span>数量</span><span>合计</span>
                            </div>
                            <div class="list">
                                <ul>
                                    <li v-for="(i,$index) in arr" :key="$index">
                                        <div class="name">
                                            <span class="remove" @click="btn(i)" title="移除本项">X</span><img :src="i.imgurl" alt=""><span class="shopname">{{i.title}}</span>
                                        </div>
                                        <div class="price">{{i.money}}</div>
                                        <div class="num">
                                            <button @click="i.n<=1?1:i.n--">-</button>
                                            <input type="text" :value="i.n">
                                            <button @click="i.n++">+</button>
                                        </div>
                                        <div class="money">
                                            &yen;{{Number(i.money.slice('1').split(',').join(''))*i.n}}
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="r_cont">
                        <div class="hd">
                            购物车总计
                        </div>
                        <div class="r_list">
                            <div>
                                <span>购物车小计</span>
                                <span>&yen;{{total}}</span>
                            </div>
                            <div>
                                <span>运费</span><span>免费送货</span>
                            </div>
                            <div>
                                <span>订单总计</span><span>&yen;{{total}}</span>
                            </div>
                        </div>
                        <div class="btn"><button>前往收银台</button></div>
                        <div class="hd">优惠券</div>
                        <div class="input">
                            <input type="text" placeholder="优惠券代码">
                        </div>
                        <div class="btns"><button>使用优惠券</button></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<style lang="scss" scoped>
    .item_box{
        max-width: 1280px;
        margin: 0 auto;
        padding-top: 50px;
        .item{
            padding: 0 40px;
            .content{
                padding: 0 5px;
                display: flex;
                .l_list{
                    width: 66%;
                    .table{
                        padding-right: 30px;
                        border-right: solid 1px #ddd;
                        .hd{
                            padding: 4px 0;
                            color: #555555;
                            font-weight: bold;
                            border-bottom: solid 3px #ddd;
                            display: flex;
                            span{
                                padding: 4px 0;
                                display: inline-block;
                                text-indent: 0.5em;
                            }
                            >span:nth-of-type(1){
                                width: 50%;
                            }
                            >span:nth-of-type(2){
                                width: 16.6%;
                            }
                            >span:nth-of-type(3){
                                width: 16.6%;
                            }
                            >span:nth-of-type(4){
                                width: 16.6%;
                                text-align: right;
                            }
                        }
                    }
                    .list{
                        ul{
                            li{
                                display: flex;
                                .name{
                                    width: 50%;
                                    .remove{
                                        width: 18px;
                                        height: 18px;
                                        line-height: 18px;
                                        text-align: center;
                                        font-weight: bold;
                                        display: inline-block;
                                        border-radius: 50%;
                                        border: solid 2px #DDDDDD;
                                        font-size: 12px;
                                        margin: 35px 0 0 10px;
                                        box-sizing: border-box;
                                        color: #ddd;
                                    }
                                    .remove:hover{
                                        border-color: #569F10;
                                        color: #569F10;
                                    }
                                    img{
                                        width: 90px;
                                        height: 90px;
                                        margin-left: 20px;
                                    }
                                    .shopname{
                                        margin-left: 20px;
                                        color: #519f10;
                                        font-size: 14px;
                                    }
                                }
                                .price{
                                    width: 16.6%;
                                    font-size: 14px;
                                    color: #777;
                                    margin-top: 35px;
                                }
                                .num{
                                    width: 16.6%;
                                    margin-top: 25px;
                                    margin-left: 10px;
                                    button{
                                        width: 26px;
                                        height: 40px;
                                        border: solid 1px #ccc;
                                    }
                                    input{
                                        width: 26px;
                                        height: 38px;
                                        border: solid 1px #ccc;
                                        border-left: none;
                                        border-right: none;
                                        text-align: center;
                                    }
                                }
                                .money{
                                    width: 16.6%;
                                    font-size: 14px;
                                    font-weight: bold;
                                    color: #777777;
                                    margin-top: 35px;
                                    text-align: right;
                                }
                            }
                        }
                    }
                }
                .r_cont{
                    flex: 1;
                    padding-left: 30px;
                    .hd{
                        color: #666;
                        font-weight: bold;
                        padding: 6px 0;
                        border-bottom: solid 3px #ddd;
                        margin-bottom: 10px;
                    }
                    .r_list{
                        div{
                            overflow: hidden;
                            >span:nth-of-type(1){
                                color: #666;
                                font-weight: bold;
                                font-size: 14px;
                                float: left;
                            }
                            >span:nth-of-type(2){
                                text-align: right;
                                font-size: 14px;
                                color: #777;
                                float: right;
                            }
                        }
                        >div:nth-of-type(1){
                            padding-bottom: 12px;
                            padding-top: 4px;
                        }
                        >div:nth-of-type(3){
                            padding: 10px 0;
                            border-bottom: solid 3px #ddd;
                        }
                    }
                    .btn{
                        margin: 20px 0;
                        button{
                            width: 100%;
                            padding-top: 12px;
                            padding-bottom: 12px;
                            color: #fff;
                            background-color: #519f10;
                            font-size: 16px;
                        }
                    }
                    .input{
                        input{
                            padding: 9px 10px 8px 10px;
                            border: solid 1px #e0e0e0;
                            width: 100%;
                            box-sizing: border-box;
                            font-size: 16px;
                        }
                    }
                    .btns{
                        margin: 20px 0;
                        button{
                            width: 100%;
                            padding-top: 12px;
                            padding-bottom: 12px;
                            color: #fff;
                            background-color: #90af6b;
                            font-size: 16px;
                        }
                    }
                }
            }
        }
    }
</style>
<script>
export default {
    data() {
        return {
            arr:[],
            price:0,
            btn(i){
                this.$store.commit('rmshoplist',i)
                this.arr = JSON.parse(localStorage.getItem('shoplist'))
                console.log(i)
            },

        }
    },
    computed:{
        total(){
            let num = 0
            for (let i = 0; i < this.arr.length; i++) {
                num+=this.arr[i].n*Number(this.arr[i].money.slice(1).split(',').join(''))
            }
            return num
        },
    },
    mounted() {
        this.arr = JSON.parse(localStorage.getItem('shoplist'))
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    },
}
</script>